import React, {useCallback, useEffect, useState} from "react";

const state = {
    ui: {
        burgerMenu: false,
        isOpenRightSidebar: false,
    },
    updateStore: () => {}
};


export const store = React.createContext(state)

export const StoreComponent = ({children}) => {
    const [value, setValue] = useState(state)

    const updateStore = useCallback((newState) => {
        setValue((oldState) => {
            return {
                ...oldState,
                ...newState
            }
        });
    }, []);

    useEffect(() => {
        updateStore({
            updateStore: updateStore
        })
    },[])

    return <div>
        <store.Provider value={value}>
            {
                children
            }
        </store.Provider>
    </div>
}