import React, {useCallback, useState} from "react";
import Main from "../../Main/Main";
import {LogInBtn, useLogInBtn} from "../../AuthBtn/AuthBtn";
import './Login.css'
import FormGroup from "../../FormUI/FormGroup/FormGroup";
import Label from "../../FormUI/Label/Label";
import Input from "../../FormUI/Input/Input";
import Button from "../../FormUI/Button/Button";
import RegisterConf from "../../RegisterConf/RegisterConf";
import LogInSocial from "../../LogInSocial/LogInSocial";
import { ReactComponent as Mail} from "../../../assets/images/mail.svg";
import PhoneField from "../../FormUI/PhoneField/PhoneField";
import DataPicker from "../../FormUI/DataPicker/DataPicker";
import Select from "../../FormUI/Select/Select";

const OrganizationData = [
    { value: 'chocolate', label: 'ЧП Ерохина' },
    { value: 'strawberry', label: 'Strawberry' },
    { value: 'vanilla', label: 'Vanilla' },
];

const Login = () => {
    const [s, setS] = useState(false)
    return <div className="Login">
        {/*<LogInBtn>LogInBtn</LogInBtn>*/}
        <Main isShowSidebar={false}>
            <button onClick={() => setS(v => !v)}>change form</button>
            {
                s ? <LoginFormFake/> : <LoginOriginal/>
            }

        </Main>
    </div>
}

const LoginOriginal = () => {
    const {onclick} = useLogInBtn()
    const onSubmit = (e) => {
        e.preventDefault()
        onclick()
    }
    return <div className="WrapperStepForm">
            <div className="LoginOriginal">
            <form className="LoginForm" onSubmit={onSubmit}>
                <div className="WrapperStepFormTitle">Войти</div>
                <div className="WrapperStepFormItems">
                    <FormGroup>
                        <Label name="E-mail"/>
                        <Input name="email"/>
                    </FormGroup>
                </div>
                <div className="WrapperStepFormItems">
                    <FormGroup>
                        <Label name="Пароль"/>
                        <Input type="password" name="password"/>
                    </FormGroup>
                </div>
                <div className="WrapperStepFormItems">
                    <Button value="Войти"/>
                </div>
                <div className="WrapperStepFormItems">
                    <RegisterConf/>
                </div>
                <div className="WrapperStepFormItems">
                    <div className="form-divider">
                        <div className="form-divider__text">или</div>
                    </div>
                </div>
                <div className="WrapperStepFormItems">
                    <LogInSocial/>
                </div>
            </form>
        </div>
    </div>
}

const LoginFormFake = () => {
    const [step, setStep] = useState(0);
    const onSubmit = (e) => {
        e.preventDefault();
        setStep((v) => ++v)
        setTimeout(() => {
            setStep((v) => ++v)
        },2000)
    }
    return <LoginForm step={step} onSubmit={onSubmit}/>
}


const LoginForm = ({onSubmit,step}) => {

    const renderStepOne = <form className="LoginForm" onSubmit={onSubmit}>
        <div className="WrapperStepFormTitle">Зарегистрироваться</div>
        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="E-mail"/>
                <Input name="email"/>
            </FormGroup>
        </div>
        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Пароль"/>
                <Input type="password" name="password"/>
            </FormGroup>
        </div>
        <div className="WrapperStepFormItems">
            <Button value="Зарегистрироваться"/>
        </div>
        <div className="WrapperStepFormItems">
            <RegisterConf/>
        </div>
        <div className="WrapperStepFormItems">
            <div className="form-divider">
                <div className="form-divider__text">или</div>
            </div>
        </div>
        <div className="WrapperStepFormItems">
            <LogInSocial/>
        </div>
    </form>

    const renderStepTwo = <div className="ConfirmMail">
        <div className="WrapperStepFormTitle">Подтвердите почту</div>
        <div className="ConfirmMail__img">
            <Mail/>
        </div>
        <div className="ConfirmMail__text">
            Чтобы подтвердить регистрацию перейдите по ссылке в письме, которое мы отправим
            вам с минуты на минуту
        </div>
    </div>

    return <div className="WrapperStepForm">
        {
            step === 0 ? renderStepOne : step === 1 ? renderStepTwo : step === 2 ? <RegisterStepThree/> : <h1>...</h1>
        }
    </div>
}

const RegisterStepThree = () => {
    return <div className="RegisterStepThree">
        <div className="WrapperStepFormTitle">Завершение регистрации</div>
        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Фамилия"/>
                <Input name="lastname"/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Имя"/>
                <Input name="name"/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Отчество"/>
                <Input name="middlename"/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Контактный телефон"/>
                <PhoneField name="phone"/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Дата рождения"/>
                <DataPicker />
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Организация"/>
                <Select placeholder="Выбрать"  data={OrganizationData}/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Отдел"/>
                <Select placeholder="Выбрать"  data={OrganizationData}/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <FormGroup>
                <Label name="Должность"/>
                <Select placeholder="Выбрать"  data={OrganizationData}/>
            </FormGroup>
        </div>

        <div className="WrapperStepFormItems">
            <Button value="Завершить регистрацию"/>
        </div>
    </div>
}

export default Login