import menuBtn from "../../assets/images/icons/menu.svg";
import './MenuBtn.css'
import {useCallback, useContext,memo} from "react";
import {store} from "../../store";
import {getBurgerMenu} from "../../selectors/ui";
import {getDispatch} from "../../selectors/getDispatch";


const MenuBtn = () => {
    const state = useContext(store);

    const onClick = useCallback(() => {
        const isBurgerMenu = getBurgerMenu(state);
        const dispatch = getDispatch(state);
        dispatch({
            ui: {
                ...state.ui,
                burgerMenu: !isBurgerMenu
            }
        })
    }, [state])


    return <div className="MenuBtn">
         <span className="MenuBtn__btn" onClick={onClick}>
            <img src={menuBtn} alt=""/>
        </span>
    </div>
}

export default memo(MenuBtn)