import './Languages.css'
import {useCallback, useMemo, useState} from "react";
import PopUp from "../PopUp/PopUp";

import uk from '../../assets/images/flags/uk.svg';
import de from '../../assets/images/flags/de.svg';
import br from '../../assets/images/flags/br.svg';
import fr from '../../assets/images/flags/fr.svg';
import getCurrentLang from "../../helpers/getCurrentLang";
import langsWithoutActiveLangHelper from "../../helpers/langsWithoutActiveLang";
import List from "../List/List";
import {LangItem} from "../ItemWithIcon/ItemWithIcon";




const Languages = () => {

    const langs = useMemo(() => [
        {
            src: uk, label: "ukraine", id: 1,
        },
        {
            src: fr, label: "Français", id: 2,
        },
        {
            src: de, label: "Deutsch", id: 3,
        },
        {
            src: br, label: "English", id: 4,
        }
    ],[])

    const [activeLang, setActiveLang] = useState(1);
    const [isShow, setIsShow] = useState(false);

    const switchPopUp = useCallback(() => {
        setIsShow((isShow) => !isShow)
    },[])

    const changeLang = useCallback((id) => {
        setActiveLang(id)
        switchPopUp()
    }, [setActiveLang,switchPopUp])

    const activeLangSrc = useMemo(() => getCurrentLang(langs, activeLang), [langs, activeLang]);
    const langsWithoutActiveLang = useMemo(() => langsWithoutActiveLangHelper(langs, activeLang),[activeLang,langs])

    return <div className="Languages">
        <div className="Languages__btn" onClick={switchPopUp}>
            <img src={activeLangSrc} alt=""/>
        </div>
        {
            isShow && <div className="Languages__popUp">
                <PopUp>
                    <List Component={LangItem} onClick={changeLang} list={langsWithoutActiveLang}/>
                </PopUp>
            </div>
        }
    </div>
}

export default Languages