import './Sidebar.css'
import {useCallback, useContext, useMemo} from "react";
import {store} from "../../store";
import {getBurgerMenu, getIsOpenRightSidebar} from "../../selectors/ui";
import {MenuSidebarR,MenuSidebarL} from "../MenuSidebar/MenuSidebar";
import {getDispatch} from "../../selectors/getDispatch";

const Sidebar = ({className, children}) => {


    return <div className={className}>
        {
            children
        }
    </div>
}

export const LeftSidebar = () => {
    const state = useContext(store);
    const isOpen = useMemo(() => getBurgerMenu(state), [state]);

    const className = `Sidebar ${false ? "Sidebar_open" : ''}`;

    return <Sidebar className={className}>
        <MenuSidebarL/>
    </Sidebar>
}


export const RightSidebar = () => {

    const state = useContext(store);
    const isOpen = useMemo(() => getIsOpenRightSidebar(state), [state]);
    const dispatch = getDispatch(state);

    const onClick = useCallback(() => {
        dispatch({
            ui: {
                ...state.ui,
                isOpenRightSidebar: !isOpen
            }
        })
    }, [state,isOpen,dispatch])

    const className = `Sidebar Sidebar_right ${false ? "Sidebar_open" : ''}`;

    return <Sidebar className={className}>
        <span className="Sidebar_btn" onClick={onClick}/>
        <MenuSidebarR/>
    </Sidebar>
}