import {useCallback} from "react";
import './ItemWithIcon.css'
import {Link} from "react-router-dom";

const ItemWithIcon = ({src, label, onClick}) => {

    return   <div className="ItemWithIcon" onClick={onClick}>
        <div className="ItemWithIcon__img-wrapper">
            <img src={src} alt="" className="ItemWithIcon__img"/>
        </div>
        <span className="ItemWithIcon__label">{label}</span>
    </div>
}

export const LangItem = (props) => {
    const {onClick, id, ...rest} = props;
    const onClickWrapped = useCallback(() => onClick(id), [id,onClick])

    return <ItemWithIcon onClick={onClickWrapped} {...rest}/>
}

export const AvatarItem = (props) => {
    const {onClick, path, ...rest} = props;
    const onClickWrapped = useCallback(() => onClick(path), [path,onClick])

    return <Link className="Link-direction-no" to={path}>
        <ItemWithIcon onClick={onClickWrapped}  {...rest}/>
    </Link>
}