import './Header.css'
import Avatar from '../Avatar/Avatar'
import Languages from "../Languages/Languages";
import Notification from "../Notification/Notification";
import {Link} from "react-router-dom";
import MenuBtn from "../MenuBtn/MenuBtn";
import {useAuth} from "../../hoc/Auth";

const Header = () => {
    return <div className="Header">
       <div className="_container">
            <div className="Header__inner">
                <div className="Header__items Header__items_no-gap">
                    <div className="Header__burger">
                       <MenuBtn/>
                    </div>
                    <span className="logo-wrapper">
                        <Link className="logo" to="/">Suprovid</Link>
                    </span>
                </div>

                <div className="Header__items">
                    <div className="Header__controls">
                        <div className="Header__controls-item">
                            <Languages/>
                        </div>
                        <div className="Header__controls-item">
                            <Notification/>
                        </div>
                    </div>
                    <Avatar/>
                </div>
            </div>
       </div>
    </div>
}

const AuthHeader = (props) => {
    const {user} = useAuth();
    return user ? <Header {...props}/> : <HeaderJust {...props}/>
}

const HeaderJust = () => {
    return <div className="Header">
        <div className="_container">
            <div className="Header__inner Header__inner_center">
                <div className="Header__items Header__items_no-gap">
                    <span className="logo-wrapper">
                        <Link className="logo" to="/">Suprovid</Link>
                    </span>
                </div>
            </div>
        </div>
    </div>
}

export default AuthHeader