import {useCallback, useState} from "react";
import './Avatar.css'
import avatar from '../../assets/images/avatar.png'
import PopUp from "../PopUp/PopUp";
import logout from '../../assets/images/icons/logout.svg'
import settings from '../../assets/images/icons/settings.svg'
// import {ReactComponent as user} from '../../assets/images/icons/user.svg'
import user from '../../assets/images/icons/user.svg'
import List from "../List/List";
import {AvatarItem} from "../ItemWithIcon/ItemWithIcon";

const routes = [
    {
        path: "/profile", label: "Профиль", src: user, id: 1,
    },
    {
        path: "/settings", label: "Настройки", src: settings, id: 2,
    },
    {
        path: "/", label: "Выход", src: logout, id: 3,
    }
]


const Avatar = () => {
    const [fullName] = useState('Александра Ерохина');
    const [isShow, setIsShow] = useState(false);

    const switchIsShow = useCallback(() => {
        setIsShow((isShow) => !isShow)
    }, [setIsShow])

    const onClick = useCallback(() => {
        switchIsShow()
    }, [switchIsShow])

    return <div className="Avatar">
        <div className="Avatar__inner" onClick={switchIsShow}>
            <div className="Avatar__img-wrapper">
                <img src={avatar} alt="" className="Avatar__img"/>
            </div>
            <div className="Avatar__text">
                {fullName}
            </div>
        </div>
        {
            isShow && <div className="Avatar__popUp">
                <PopUp>
                    <List Component={AvatarItem} onClick={onClick} list={routes}/>
                </PopUp>
            </div>
        }
    </div>
}

export default Avatar


